  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

body {
  font-family: 'Open Sans', sans-serif;
  font-size: 12px;
  background-color: whitesmoke;
}

.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  padding: 8px;
  background-color: #bf1722;
  color: white;
  z-index: 100;
}

.skip-link:focus {
  top: 0;
}

.header {
  background-color: #5DEBD7;
}

.header__inner {
  width: 100%;
  max-width: 1200px;
  padding-block: 1rem;
  margin-inline: auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.header .header__logo img {
  width: 150px;
}

.header .header__menu {
  background-color: transparent;
  border: none;
  font-size: 32px;
  margin: 10px;
  display: block;
  width: 44px;
  height: 44px;
  text-align: center;
  text-decoration: none;
  color: black;
}

.header .nav ul.nav__list {
  padding-left: 0;
  display: flex;
  justify-content: center;
  gap: 2rem 1rem;
}

.header .nav li.nav__item {
  list-style-type: none;
}

.header .nav li.nav__item a {
  display: inline-block;
  padding: 0.8rem 1.5rem;
  border-radius: 999px;
  text-decoration: none;
  font-size: 1.05rem;
  font-weight: 500;
  color: black;
  transition: all 0.2s ease-in-out;
}

.header .nav li.nav__item:hover a {
  background-color: #074173;
  color: white;
}

main {
  width: 100%;
}

.content {
  padding: 32px;
  margin: 0 auto;
}

.explore-restaurant, .favorite-restaurant, .selected-menu {
  width: 100%;
  margin: 60px auto;
  text-align: center;
}

.explore-restaurant__label, .favorite-restaurant__label, .selected-menu__label {
  font-size: 24px;
  font-weight: 700;
  color: #074173;
}

.explore-restaurant__label::after, .favorite-restaurant__label::after, .selected-menu__label::after {
  content: '';
  margin-top: 16px;
  display: block;
  border-bottom: 1px solid #074173;
}

.restaurants, .selected-menus {
  margin: 32px auto auto;
  text-align: left;
  display: grid;
  grid-row-gap: 16px;
}

.restaurant-item__not__found {
  width: 100%;
  text-align: center;
  grid-column-start: 1;
  grid-column-end: -1;
}

.restaurant-item, .selected-menu-item {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  width: 100%;
  border-radius: 5px;
  overflow: hidden;
  background-color: white;
}

.restaurant-item__content, .selected-menu-item__content {
  padding: 16px;
}

.restaurant-item__thumbnail, .selected-menu-item__thumbnail {
  width: 100%;
}

.restaurant-item__rating {
  font-size: 14px;
  color: orange;
  font-weight: 500;
}

.restaurant-item__name, .selected-menu-item__name {
  font-weight: 500;
  font-size: 16px;
  padding: 0.8rem 0;
  transition: 0.3s opacity;
}

.restaurant-item__name {
  margin-top: 8px;
}

.restaurant-item__name:hover, .selected-menu-item__name:hover {
  opacity: 0.5;
}

.restaurant-item__name a, .selected-menu-item__name a {
  text-decoration: none;
  padding: 0.8rem 0;
  color: inherit;
}

.restaurant-item__location {
  font-size: 12px;
  color: #777;
  margin-top: 8px;
}

.selected-menu-item__restaurant {
  font-size: 16px;
  color: #555;
  margin-top: 8px;
  padding: 0.8rem 0;
  transition: 0.3s opacity;
}

.selected-menu-item__restaurant:hover {
  opacity: 0.5;
}

.selected-menu-item__restaurant a {
  text-decoration: none;
  color: inherit;
  padding: 0.8rem 0;
}

.restaurant-item__description {
  margin-top: 16px;
  font-size: 12px;
  line-height: 1.5em;
}

.selected-menu-item__price {
  margin-top: 8px;
  font-size: 16px;
}

.restaurant {
  margin: 8px auto;
  padding: 16px;
  width: 100%;
  max-width: 800px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 18px 16px;
  background-color: white;
  font-size: 14px;
}
 
.restaurant .restaurant__name {
  font-size: 18px;
}

.restaurant .restaurant__image {
  width: 100%;
  max-width: 400px;
}

.restaurant .restaurant__info h3 {
  font-size: 16px;
}

 
.restaurant .restaurant__info h4 {
  font-size: 14px;
  margin: 8px 0;
}

.restaurant .restaurant__description h3 {
  font-size: 16px;
}

.restaurant .restaurant__description p {
  line-height: 1.5em;
}

.restaurant .restaurant__menus h3 {
  padding-bottom: 8px;
  font-size: 16px;
}

.restaurant .restaurant__menus section h4 {
  padding-bottom: 8px;
  font-size: 14px;
}

.restaurant .restaurant__menus section .restaurant__food-menus {
  padding-bottom: 8px;
}

.restaurant .restaurant__menus section ol {
  margin: 0 24px;
}

.restaurant .restaurant__menus section ol li {
  line-height: 1.5em;
}

.restaurant .restaurant__reviews h3 {
  padding-bottom: 8px;
  font-size: 16px;
}

.restaurant .restaurant__reviews > form > .input {
  margin: 8px 0;
}

.restaurant .restaurant__reviews > form > button {
  background-color: #074173;
  color: white;
  border: 0;
  border-radius: 5px;
  display: block;
  width: 100%;
  min-height: 44px;
  padding: 8px;
  font-size: 14px;
  cursor: pointer;
}

.restaurant .restaurant__reviews > form > button > span {
  font-weight: bold;
}

.restaurant .restaurant__reviews > form > .input > input {
  display: block;
  width: 100%;
  min-height: 44px;
  padding: 8px;
  border-radius: 5px;
  font-size: 14px;
}

.restaurant .restaurant__reviews > form > .input > label {
  color: #074173;
  font-weight: bold;
  font-size: 14px;
}

.restaurant .restaurant__reviews .restaurant__customer-reviews {
  padding-bottom: 8px;
}

.restaurant .restaurant__reviews .restaurant__customer-reviews .restaurant__review-item {
  padding: 8px 16px 16px 16px;
  border: 1px solid black;
  border-radius: 5px;
  margin: 10px 0;
}

.restaurant .restaurant__reviews .restaurant__customer-reviews .restaurant__review-item > h4, p {
  margin: 8px 0;
}

.restaurant .restaurant__reviews .restaurant__customer-reviews .restaurant__review-item h4 {
  color: blue;
}

.restaurant .restaurant__reviews .restaurant__customer-reviews .restaurant__review-item span {
  font-style: oblique;
}

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #5DEBD7;
  
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  
  font-size: 18px;
  color: black;
  cursor: pointer;
}

footer {
  font-weight: bold;
  font-size: 12px;
  background-color: #1679AB;
  color: white;
  padding: 2em;
  width: 100%;
  text-align: center;
